:root {
  --container-width: 340px;
  --card-spacing: 20px;
  --system-font: -apple-system, BlinkMacSystemFont, "SF Pro Display", "SF Pro Text", "Helvetica Neue", sans-serif;
}

.page-container {
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(
    45deg,
    #2d1259,
    #3a1c71,
    #2b1463,
    #421b6f,
    #331667,
    #2c1154,
    #3d1a75
  );
  background-size: 400% 400%;
  animation: gradientBG 30s ease infinite;
  display: flex;
  justify-content: center;
  padding: 20px;
}

@keyframes gradientBG {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.content-container {
  width: 100%;
  max-width: var(--container-width);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 10px;
}

.image1 {
  width: 340px;
  height: 120px;
  margin: 2px 0;
  margin-left: 35px;
  cursor: pointer;
  object-fit: contain;
}

.image1:hover {
  transform: scale(1.15);
  transition: transform 0.3s ease;
}

.games-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: var(--card-spacing);
  width: 100%;
  padding: 0;
  margin-top: -20px;
}

.games-grid .game-card {
  width: 100%;
  height: 320px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 15px;
  border-radius: 12px;
  position: relative;
}

.games-grid .game-card img {
  width: auto;
  height: 85%;
  max-height: 260px;
  object-fit: contain;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.float-animation {
  animation: floatY 3s ease-in-out infinite;
}

.bounce-animation {
  animation: bounceY 2s ease-in-out infinite;
}

.rotate-animation {
  animation: rotateAround 8s linear infinite;
}

@keyframes floatY {
  0%, 100% {
    transform: translate(-50%, -50%);
  }
  50% {
    transform: translate(-50%, -60%);
  }
}

@keyframes bounceY {
  0%, 100% {
    transform: translate(-50%, -50%);
  }
  50% {
    transform: translate(-50%, -65%);
  }
}

@keyframes rotateAround {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

.games-grid .game-card:nth-child(3n+1) img {
  animation: floatY 3s ease-in-out infinite;
}

.games-grid .game-card:nth-child(3n+2) img {
  animation: bounceY 2s ease-in-out infinite;
}

.games-grid .game-card:nth-child(3n) img {
  animation: rotateAround 8s linear infinite;
}

.games-grid .game-card img:hover {
  animation-play-state: paused;
  transform: translate(-50%, -50%) scale(1.1);
}

.games-grid .play-button {
  width: 80%;
  padding: 12px 20px;
  font-size: 16px;
  border-radius: 25px;
  position: absolute;
  bottom: 20px;
  left: 53%;
  transform: translateX(-50%);
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 0.8px;
  cursor: pointer;
  font-family: var(--system-font);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media screen and (max-width: 375px) {
  .games-grid .game-card {
    height: 300px;
  }
  
  .games-grid .game-card img {
    height: 80%;
    max-height: 240px;
  }
}

@media screen and (min-width: 390px) {
  .games-grid .game-card {
    height: 340px;
  }
  
  .games-grid .game-card img {
    height: 90%;
    max-height: 280px;
  }
}

/* Стили для кнопок разных игр */
.play-button-red {
  background: #1a0000;
  border: 3px solid #ff0000;
  box-shadow: 0 0 20px rgba(230, 57, 70, 0.8);
  color: #ff0000;
}

.play-button-gold {
  background: #1a0d00;
  border: 3px solid #ffa500;
  box-shadow: 0 0 20px rgba(255, 215, 0, 0.8);
  color: #ffa500;
}

.play-button-purple {
  background: #12001f;
  border: 3px solid #9400d3;
  box-shadow: 0 0 20px rgba(94, 96, 206, 0.8);
  color: #9400d3;
}

.play-button-blue {
  background: #000019;
  border: 3px solid #0077be;
  box-shadow: 0 0 20px rgba(72, 202, 228, 0.8);
  color: #0077be;
}

@keyframes swing {
  0% {
    transform: translateY(0) rotate(0deg) scale(1);
  }
  25% {
    transform: translateY(-5px) rotate(-5deg) scale(1.05);
  }
  75% {
    transform: translateY(5px) rotate(5deg) scale(1.05);
  }
  100% {
    transform: translateY(0) rotate(0deg) scale(1);
  }
}

@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.05);
    opacity: 0.8;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes float {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0px);
  }
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-15px);
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Медиа-запрос для iPhone 15 */
@media screen and (width: 393px) {
  .content-container {
    max-width: 360px;
  }
  
  .image1 {
    margin-left: 45px;
  }
}


